body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.title{
	width: 100%;
	text-align: center;
    color: #D23943;
    font-size: 5em;
}

.subtitle{
	color: #D23943;
	font-size: 3em;
	width: 100%;
	text-align: center;
}

.tinyTitle{
	font-weight: bold;
	width: 100%;
	text-align: center;
}


.archived{
	border: 2px solid black;
	border-radius: 10px;
	margin-top: 150px;
	margin-bottom: 160px;
	width: 75%;
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 60px;
	padding-top: 20px;
	padding-right: 25px;
	padding-left: 25px;
}

.live{
	display: inline-flex;
	align-items: center;
	color: white;
	background-color: #D23943;
	padding-bottom: 2px;
	cursor: pointer;
	transition: transform 300ms;
	backface-visibility: hidden;
}

.live:hover{
	transform: scale(1.05);
}

.live:hover > .circle{
	animation: blink 1s steps(1, start) infinite;
}

.circle{
	height: 30px;
	width: 30px;
	background-color: white;
	border-radius: 50%;
	display: inline-block;
	margin-bottom: -1px;
}

.videoGrid{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
	grid-gap: 50px;
	width: 85%;
	margin-left: auto;
	margin-right: auto;
}

@media only screen and (max-width: 445px) {
	.videoGrid {
		grid-template-columns: repeat(1, 100%);
	}
}

.videoWrapper{
	position: relative;
	padding-bottom: 56.25%;
}

.video{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.75);

	border-radius: 10px;

	/* The background color makes the squares visible even when the iFrame isn't loaded compeltely */
}

.horizontal{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	width: 300px;
	margin-left: auto;
	margin-right: auto;
}


.lightText{
	text-align: center;
	width: 100%;
	margin-top: 50px;
	margin-bottom: 50px;
	opacity: 0.75;
}

.footer{
	width: 100%;
	text-align: center;
	margin-top: 150px;
}

@keyframes blink{
	0%{ opacity: 1}
	50%{ opacity: 0; }
}

.popupContainer{
	position: fixed;
	left: 0;
	bottom: -300px;
	margin: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #D23943;
	padding: 20px;
	padding-left: 50px;
	padding-right: 50px;
	border-top: solid black 3px;
	transition: opacity 1.5s ease-in, bottom 2s, visibility 2s;
}

@media only screen and (max-width: 500px) {
	.popupContainer {
		padding: 10px;
	}
}

.xBtn{
    border: 0;
    background-color: transparent;
    font-size: 2.5em;
    font-family: Arial, Helvetica, sans-serif;
    z-index: 100;
    margin: 5px;
    margin-top: 15px;
    cursor: pointer;
    transform: rotate(45deg);
    transition: transform 1s;
    backface-visibility: hidden;
    margin-bottom: 10px;
}

.xBtn:hover{
    transform: rotate(135deg);
}

